.background {
    background-color:rgb(249, 249, 249);
}

.title-feed {
    font-weight: 500;
    font-size: 25px;
    font-family: 'Grand Hotel', cursive;
    color: black;
}

.title-feed-mobile {
    font-weight: 500;
    font-size: 20px;
    font-family: 'Grand Hotel', cursive;
    color: black;
}

.no-posts {
    margin-top: 30px;
    font-size: 30px;
}

.mobile {
    background-color: rgb(249, 249, 249);
    max-width: 100%;
}

.navbar-feed {
    height: 50px;
    border-color: black;
    border-style: solid;
    border-width: 1px;
    border-top: 0px;
    border-right: 0px;
    border-left: 0px;
    display: flex;
    flex-direction: row;
    justify-content: center;
}

.navbar-feed-mobile {
    height: 40px;
    width: 100%;
    max-width: 100%;
    border-color: black;
    border-style: solid;
    border-width: 2px;
    border-top: 0px;
    border-right: 0px;
    border-left: 0px;
    display: flex;
    flex-direction: row;
    justify-content: center;
}

.feed-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 50px;
    max-width: 100% !important;
}

.feed-container-mobile {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 40px;
    max-width: 100% !important;
}

.feed-info-container {
    margin-top: 50px;
    display: flex;
    flex-direction: row;
    justify-content: center;
}

.scroll-col {
    padding: 0px;
    width: 600px !important;
}

.info-card {
    margin-top: 50px;
    margin-left: 10px;
    width: 300px;
    height: 400px;
    position: fixed;
}