.post-card {
    height: auto;
    width: auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 30px;
    margin-bottom: 30px;
}

.card-body {
    padding: 0px;
    padding-top: 10px;
    padding-bottom: 10px;
    padding-right: 0px;

}

.header {
    display: flex;
    flex-direction: row;
    justify-content: left;
    align-items: flex-start;
    margin-bottom: 10px;
    margin-left: 10px;
}

.post-mobile {
    max-width: 100%;
    width: auto;
    margin-bottom: 25px;
}

.header-mobile {
    max-width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: left;
    align-items: flex-start;
    margin-bottom: 10px;
    margin-left: 10px;
    margin-top: 10px;
}

.profile-image-wrapper {
    width: 40px;
    height: 40px;
    display:block;
    /* div height to be the same as width*/

    /* make it a circle */
    border-radius:50%;

    /* Centering on image`s center*/
    background-position-y: center;
    background-position-x: center;
    background-repeat: no-repeat;

    /* it makes the clue thing, takes smaller dimension to fill div */
    background-size: cover;
}

.name {
    font-size: 14px;
    padding-top: 8px;
    padding-left: 8px;
}

.profile-image {
    width: 100%;
    height: 100%;
    border-radius: 40px;
}

.post-image-wrapper {
    width: 600px;
}

.post-image-wrapper-mobile {
    max-width: 100%;
    width: auto;
    height: auto;

}

.post-image {
    max-width: 100%;
    max-height: 100%;
}

.post-caption {
    font-size: 15px;
    line-height: 1.3;
    width: 500px;
    padding-left: 10px;
    padding-right: 5px;
    padding-top: 20px;
    padding-bottom: 10px;
}

.post-caption-mobile {
    font-size: 15px;
    line-height: 1.3;
    max-width: 100%;
    padding-left: 10px;
    padding-right: 5px;
    padding-top: 15px;
    padding-bottom: 10px;
}

.post-date {
    color: gray;
    font-size: 13px;
    padding-left: 10px;
}


